import React, { useState, useRef, useEffect } from "react";
import { useGesture } from "@use-gesture/react";
import "../../CSS/Home/Profile.css";
import { Button } from "primereact/button";
import { Dialog } from "primereact/dialog";
import AvatarEditor from "react-avatar-editor";

export default function Profile() {
  const [visible, setVisible] = useState(false);
  const [selectedImage, setSelectedImage] = useState(null);
  const [profileImage, setProfileImage] = useState(
    "https://templates.joomla-monster.com/joomla30/jm-news-portal/components/com_djclassifieds/assets/images/default_profile.png"
  );
  const [scale, setScale] = useState(1.2); // initial zoom scale
  const targetScaleRef = useRef(1.2);
  const editorRef = useRef(null);

  // Prevent background scroll when dialog is open
  useEffect(() => {
    if (visible) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
    return () => {
      document.body.style.overflow = "auto";
    };
  }, [visible]);

  // Smooth zoom animation
  useEffect(() => {
    let animationFrame;
    const animateScale = () => {
      setScale((prev) => {
        const diff = targetScaleRef.current - prev;
        if (Math.abs(diff) < 0.01) return targetScaleRef.current;
        return prev + diff * 0.1;
      });
      animationFrame = requestAnimationFrame(animateScale);
    };
    animationFrame = requestAnimationFrame(animateScale);
    return () => cancelAnimationFrame(animationFrame);
  }, []);

  // File input handler with reset
  const onSelectFile = (e) => {
    if (e.target.files && e.target.files.length > 0) {
      setSelectedImage(e.target.files[0]);
      setVisible(true);
    }
    e.target.value = "";
  };

  // Crop & Save functionality
  const handleCropSave = () => {
    if (editorRef.current) {
      const canvas = editorRef.current.getImageScaledToCanvas();
      const dataUrl = canvas.toDataURL();
      setProfileImage(dataUrl);
    }
    setVisible(false);
  };

  // Mouse wheel handler for zoom (desktop)
  const handleWheel = (e) => {
    e.preventDefault();
    if (e.deltaY < 0) {
      targetScaleRef.current = Math.min(targetScaleRef.current + 0.1, 3);
    } else if (e.deltaY > 0) {
      targetScaleRef.current = Math.max(targetScaleRef.current - 0.1, 1);
    }
  };

  // Pinch zoom for mobile using react-use-gesture
  const bind = useGesture({
    onPinch: ({ offset: [d] }) => {
      targetScaleRef.current = Math.min(Math.max(d, 1), 3);
    },
  });

  // Dialog footer with Save button
  const footerContent = (
    <div className="PhotoSave">
      <Button
        className="SubPhotoSave"
        label="Save"
        icon="pi pi-check"
        onClick={handleCropSave}
        autoFocus
      />
    </div>
  );

  return (
    <div>
      {/* Dialog for cropping image */}
      <div className="card PhotoBox">
        <Dialog
          className="SubPhotoBox"
          visible={visible}
          onHide={() => setVisible(false)}
          footer={footerContent}
          resizable={false}
        >
          <h2>Update Profile</h2>
          {selectedImage ? (
            <>
              <div {...bind()} onWheel={handleWheel}>
                <AvatarEditor
                  ref={editorRef}
                  image={selectedImage}
                  width={300}
                  height={300}
                  border={0}
                  borderRadius={200}
                  color={[255, 255, 255, 0.6]}
                  scale={scale}
                  rotate={0}
                />
              </div>
            </>
          ) : (
            <p>No Image Selected</p>
          )}
        </Dialog>
      </div>

      {/* Main Profile Section */}
      <div id="ProfieMainContainer">
        <div className="UserProfile">
          <div>
            <div className="SubProfile">
              <div className="UserImage">
                <img src={profileImage} alt="Profile" />
              </div>
              <div
                className="UserProfileEdite"
                onClick={() => document.getElementById("fileInput").click()}
              >
                <p>
                  <i className="ri-edit-2-fill"></i>
                </p>
              </div>
              <div className="UserName">
                <h3>Suraj Pujari</h3>
              </div>
            </div>
            <div className="UserProfileSection">
              <div className="PersonalInfo PersonalInformation">
                <span>
                  <i className="ri-user-fill"></i>
                </span>
                <span>Personal Info</span>
              </div>
              <div className="LogOut PersonalInformation">
                <span>
                  <i className="ri-logout-box-r-line"></i>
                </span>
                <span>Log Out</span>
              </div>
            </div>
          </div>
        </div>

        <div className="ProfileUserInfo">
          <h3>Personal Inforamation</h3>
          <div>
            <form action="">
              <div className="Pesonal-Info">
                <div className="Fname">
                  <label htmlFor="fname">First Name</label>
                  <br />
                  <input type="text" id="fname" name="fname" required />
                </div>
                <div className="Lname">
                  <label htmlFor="lname">Last Name</label>
                  <br />
                  <input type="text" id="lname" name="lname" required />
                </div>
              </div>
              <div className="UserEmail">
                <label htmlFor="email">Email</label>
                <br />
                <input type="email" required />
                <br />
                <label htmlFor="text">Address</label>
                <br />
                <input type="text" required />
              </div>
              <div className="GenderInput">
                <div className="UserNumber">
                  <label htmlFor="phone">Phone Number</label>
                  <br />
                  <input type="number" id="phone" name="phone" required />
                </div>
                <div className="Gender">
                  <label htmlFor="male">Male</label>
                  <input
                    type="radio"
                    id="male"
                    name="gender"
                    value="male"
                    required
                  />
                  <label htmlFor="female">Female</label>
                  <input
                    type="radio"
                    id="female"
                    name="gender"
                    value="female"
                    required
                  />
                </div>
              </div>
              <div className="InfoSave">
                <button className="SaveChanges">Save Changes</button>
                <button className="DiscardChanges">Discard Changes</button>
              </div>
            </form>
          </div>
        </div>
      </div>

      {/* Hidden file input for image upload */}
      <input
        type="file"
        accept="image/*"
        id="fileInput"
        style={{ display: "none" }}
        onChange={onSelectFile}
      />
    </div>
  );
}
