import React, { useState, useEffect, useRef } from 'react';
import { useNavigate } from 'react-router-dom';
import '../../CSS/Home/Navbar.css';
import menu from '../../Assets/SVG/menu.svg';
import closemenu from '../../Assets/SVG/close-menu.svg';
import { motion } from 'framer-motion';
import user from '../../Assets/SVG/sign-in-user.svg';
import { Link } from 'react-router-dom';

export default function Navbar() {
  const [isOpen, setIsOpen] = useState(false);
  const [showMobileNavbar, setShowMobileNavbar] = useState(true);
  const navigate = useNavigate();
  const menuRef = useRef(null);
  const lastScrollY = useRef(0);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = "hidden";
    } else {
      document.body.style.overflow = "auto";
    }
    return () => {
      document.body.style.overflow = "auto";
    };
  }, [isOpen]);

  useEffect(() => {
    const handleClickOutside = (e) => {
      if (menuRef.current && !menuRef.current.contains(e.target)) {
        setIsOpen(false);
      }
    };

    if (isOpen) {
      document.addEventListener("mousedown", handleClickOutside);
    } else {
      document.removeEventListener("mousedown", handleClickOutside);
    }
    return () => {
      document.removeEventListener("mousedown", handleClickOutside);
    };
  }, [isOpen]);

  useEffect(() => {
    const handleScroll = () => {
      if (window.innerWidth <= 600) {
        if (window.scrollY > lastScrollY.current) {
          setShowMobileNavbar(false);
        } else {
          setShowMobileNavbar(true);
        }
        lastScrollY.current = window.scrollY;
      }
    };
    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, []);

  return (
    <>
      <div id="main">
        <nav>
          {/* Mobile Navbar */}
          <div id="mob-nav">
            <div id="mobile-navbar" className={`mobile-navbar ${showMobileNavbar ? '' : 'hidden'}`} >
              <div className="menu-toggle" onClick={toggleMenu}>
                <img src={menu} alt="Open Menu" />
              </div>
              <div id="mob-login" onClick={() => navigate("/Login")}>
                <Link to="/Login">
                  Sign in <img src={user} id="user" alt="User" />
                </Link>
              </div>
            </div>

            {/* Side Menu */}
            <div className="menu-container">
              <motion.div ref={menuRef} className={`side-menu ${isOpen ? 'open' : 'closed'}`} initial={{ x: '-100%' }} animate={{ x: isOpen ? 0 : '-100%' }} transition={{ duration: 0.3 }} >
                
                <div className="menu-list">
                  <div id="close-menu">
                    <h3>Menu</h3>
                    <img onClick={toggleMenu} src={closemenu} alt="Close Menu" />
                  </div>
                  <ul onClick={() => navigate("/HomeXpress.com")}>
                    <li>
                      <Link to="/HomeXpress.com">
                        <i className="ri-home-5-fill menu-svg"></i> Home
                      </Link>
                    </li>
                  </ul>
                  <ul onClick={() => navigate("/Register")}>
                    <li>
                      <Link to="/Register">
                        <i className="ri-user-fill menu-svg"></i> Seller Login
                      </Link>
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <Link to="/MainServices">
                        <i className="ri-discount-percent-fill menu-svg"></i> Offer Zone
                      </Link>
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <Link to="/Profile">
                        <i className="ri-account-circle-fill menu-svg"></i> My Account
                      </Link>
                    </li>
                    <li>
                      <Link to="/">
                        <i className="ri-takeaway-fill menu-svg"></i> My Orders
                      </Link>
                    </li>
                  </ul>
                  <ul>
                    <li>
                      <Link to="/">
                        <i className="ri-customer-service-2-fill menu-svg"></i> Help Center
                      </Link>
                    </li>
                  </ul>
                </div>
              </motion.div>
            </div>

            {isOpen && (
              <div className="menu-overlay" onClick={toggleMenu}></div>
            )}
          </div>

          {/* Desktop Navbar */}
          <div id="desktop-nav">
            <div id="nav-part1">
              <input type="text" placeholder="Search for Services :)" />
              <span>
                <i className="ri-search-line"></i>
              </span>
            </div>
            <div id="nav-part2">
              <Link to="/HomeXpress.com" className="a-menu">
                <i className="ri-home-heart-fill"></i> Home
              </Link>
              <Link to="/Login" className="a-menu">
                <i className="ri-login-box-line"></i> Login
              </Link>
            </div>
            <div id="user-profile">
              <Link to="/Profile">
                <i className="ri-account-circle-line"></i>
              </Link>
            </div>
          </div>
        </nav>
      </div>
    </>
  );
}
