export default function Booking() {
  const navigate = useNavigate();
  const { serviceId } = useParams(); // Dynamic parameter from URL

  // Agar serviceId match nahi hota, toh default "ac" service data use karein
  const service = serviceData[serviceId] || serviceData["ac"];

  return (
    <div id='Booking-Container'>
      <div id='Main-Booking'>
        <div className='Booking-left'>
          <div className='Booking-Img'>
            <img src={service.img} alt={service.heading} />
          </div>
          
        </div>

        <div className='Booking-Info'>
          <div className='Booking-heading'>
            <h1>{service.heading}</h1>
          </div>

          <div className='Booking-Rating'>
            <div>
              {Array.from({ length: service.rating }).map((_, index) => (
                <span key={index}><i className="ri-star-s-fill"></i></span>
              ))}
            </div>
            <div className='booking-review'>
              <p>({service.reviews} reviews)</p>
            </div>
          </div>

          <div className='booking-price'>
            <span className='booking-main-price'>{service.price}</span>
            <span className='booking-discount-price'><strike>{service.discountPrice}</strike></span>
            <span className='booking-discount'>{service.discount}</span>
          </div>

          <div className='Booking-Description'>
            <p>{service.description}</p>
          </div>
          
          <div className='Booking-Button'>
            <button className='Book-now' onClick={() => navigate("/BillingDetails", { state: { price: service.price } })}>
              <i className="ri-heart-fill"></i> Book now
            </button>
          </div>
        </div>
      </div>
    </div>

  );
}
